import React, {useState} from 'react';
import ObjectProperties from './ObjectPropertiesMenu';
import WorldProperties from './WorldPropertiesMenu';

function PropertiesMenu({objectProperties,worldProperties,resetScene,stop,erase}) {
    const [isTabOpen, setOpenTab] = useState(2);


  return (
  
  <div className='properties-menu'>

      <div className="proprty-tabs-pane">

          <div className="property-tab-button" style = {
              {backgroundColor: isTabOpen == 1 ? 'rgb(80, 79, 79)' : 'rgb(53, 53, 53)', borderBottom : isTabOpen == 1 ? 0 : '2px solid black'}} 
              onClick= {() => {setOpenTab(1)}}>World Properties</div>

          <div className="property-tab-button" style = {
              {backgroundColor: isTabOpen == 2 ? 'rgb(80, 79, 79)' : 'rgb(53, 53, 53)', borderBottom : isTabOpen == 2 ? 0 : '2px solid black'}} 
              onClick= {() => {setOpenTab(2)}}>Object Properties</div>

      </div>

     <div className="property-tab">
        {/* {isTabOpen == 2 && <ObjectProperties currentObjectProperties={currentObjectProperties} properties={properties}/>}
        {isTabOpen == 1 && <WorldProperties erase={erase} stop={stop} funct={funct} worldProperties={worldProperties}/>}  */}
        
      <ObjectProperties Index = {isTabOpen == 2 ? 1 : 0} objectProperties={objectProperties}/>
        <WorldProperties Index = {isTabOpen  == 1 ? 1 : 0} erase={erase} stop={stop} resetScene={resetScene} worldProperties={worldProperties}/>

        {/* {isTabOpen == 2 && <ObjectProperties/>}
        {isTabOpen == 1 && <WorldProperties />} */}



     </div>
     
  </div>
  
  );
}

export default PropertiesMenu;
