import {React , useState , useEffect} from 'react';
import {Line} from 'react-chartjs-2';
import { Chart as ChartJS } from 'chart.js/auto'
import { Chart } from 'react-chartjs-2'


const BarGraph = ({testP,ticks,stopGraphing}) => {


    let [data,setData] = useState([])
    let [time,setTime] = useState([])

    useEffect(() => {
        // let a = data.push(testP)
        setData(prevArray => [...prevArray,testP])
        setTime(prevArray => [...prevArray,ticks])
        // console.log(data)

    },[testP])

    const clearGraph = function(){
      setData([])
      setTime([])
    }

  return (

  <div className="bar-graph">


    <Line 

    data = {{
        labels: time,
        datasets: [{
          label: "Y velocity",
          data: data,
          lineTension: 0.01,
          fill: false,
          borderColor: 'rgb(30, 215, 96)',
          backgroundColor: 'transparent',
          pointBorderColor: 'rgb(30, 215, 96)',
          pointBackgroundColor: 'rgb(30, 215, 96)',
          // borderDash: [5, 5],
          // pointRadius: 5,
          // pointHoverRadius: 10,
          // pointHitRadius: 30,
          // pointBorderWidth: 2,
          // pointStyle: 'rectRounded'
        }]
      }}
      height = {20}
      width = {20}
    options = {{
        maintainAspectRatio : false
    }}
    
    />

    <div style={{padding:"15px",margin:"auto",display:"flex",alignContent:"space-around"}}>
      <button style={{width:"50%",height:"4.5vh",marginRight:'20px'}}>Play/Stop</button>
      <button onClick={() => {stopGraphing();clearGraph()}} style={{width:"50%",height:"4.5vh"}}>Reset</button>

    </div>

  </div>
  
  );
}

export default BarGraph;


